/**
 *  Modals
 */
@import 'mixins.less';

@zindexModal: 999;
@zindexModalBackdrop: 1;
@zindexModalBody: 2;

// Base modal
.modal {
  position: fixed;
  z-index: @zindexModal;
  width: 100%;
  height: 100%;
  text-align: center;
  left: 0;
  top: 0;

  .modal-dialog {
    font-size: 14px;
    background-color: @white;
    vertical-align: middle;
    margin: 0 18px;
    .box-shadow(0 1px 2px rgba(0, 0, 0, 0.1));
  }

  .modal-content {
    padding: 27px 17px 30px;
  }

  .modal-header {
    text-align: center;
    .modal-title {
      font-size: 20px;
      line-height: 22px;
    }
  }

  // Body (where all modal content resides)
  .modal-body {
    position: relative;
    overflow-y: auto;
    padding: 6px 0;
    font-size: 16px;
    z-index: @zindexModalBody;
    vertical-align: top;
  }

  // Footer (for actions)
  .modal-footer {
    margin-top: 18px;
    display: -webkit-box;
    display: box;

    .modal-footer-left {
      box-flex: 3;
      -webkit-box-flex: 3;
      text-align: center;
    }
    .modal-footer-right {
      box-flex: 2;
      -webkit-box-flex: 2;
      width: 108px;
      text-align: center;
    }

    // Properly space out buttons
    .btn + .btn {
      margin-left: 5px;
      margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs
    }
    // but override that for button groups
    .btn-group .btn + .btn {
      margin-left: -1px;
    }
    // and override it for block buttons as well
    .btn-block + .btn-block {
      margin-left: 0;
    }
  }

  &.hide {
    display: none;
  }

  &.show {
    display: inline-block;
  }
}

// Background
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindexModalBackdrop;
  background-color: @black;
  // Fade for backdrop
  &.fade {
    opacity: 0;
  }
}

.modal-backdrop,
.modal-backdrop.fade.in {
  opacity: 0.8;
}
